@import url('https://fonts.googleapis.com/css?family=Open+Sans&display=swap');
@import url('https://fonts.googleapis.com/css?family=Mansalva&display=swap');

body {
  background-color:#FFFFFF;
  /* min-height: 100vh; */
  display: flex;
  flex-direction: column;
  /* align-items: center;   */
  /* text-align: center; */
  /* justify-content: center; */
  /* font-size: calc(10px + 2vmin); */
  color: black;
  font-family: Open Sans;
}

.impact-button {
  cursor: pointer;
  font-family: Open Sans;
  font-weight: bold;
  color: white;
  background: #BB6BD9;
  border: 3px dotted #FFFFFF;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
  border-radius: 8px;
  font-family: Open Sans;
  outline: none;
}

button:hover {
  box-shadow: 0px 8px 8px rgba(0, 0, 0, 0.25);
}

.header {
  color: #4F4F4F;
  display: flex;
  justify-content: center;
  /* flex-direction: column; */
  /* align-content: center; */
  /* background-color: white; */
}

  .menu {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.25);
    border-radius: 0px 0px 8px 8px;
    background-color: #FFFFFF;
    padding-left: 150px;
    padding-right: 150px;
    max-width: 900px;
  }

    .logo {
      padding: 10px;
    }

    a:link, a:visited, a:active {
      text-decoration: none;
      color: #4F4F4F;
      font-weight: normal;
    }
    
    a:hover {
      text-decoration: underline;
      color: #4F4F4F;
    }
    
    .current{
      font-weight: bold;
    }

      .desktop {
        list-style-type: none;
        display: flex;
        margin-right: 1em;
      }

        .impact-button-menu {
          font-style: normal;
          font-weight: bold;
          font-size: 14px;
          line-height: 24px;
        }

        li {
          margin-left: 1em;
          list-style-type: none;
        }

      .hamburger, .mobile {
        display: none;
      }

        .nav {
          font-family: Open Sans;
          font-size: 14px;
          line-height: 40px;
        }

.item-intro {
  display: flex;
  padding: 20px;
  align-items: center;
  font-family: Open Sans;
  font-style: normal;
  font-size: 18px;
  line-height: 24px;
  width: 370px;
  height: 80px;
  margin: 20px;
}

      @media only screen and (max-width: 1000px) {

        /* body {
          font-size: calc(10px + 2vmin);
        } */

        .header {
          color: #4F4F4F;
          display: flex;
          /* justify-content: center; */
          flex-direction: column;
          /* align-content: center; */
          background-color: white;
          text-align: center;
          /* box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.25); */
        }

        .nav {
          color: #4F4F4F;
        }

        .desktop {
          display: none;
        }

        .mobile {
          display: block;
        }

        .menu {
          /* justify-content: center; */
          /* width: 100%; */
          display: flex;
          justify-content: flex-start;
          padding: 0px;
        }

        .logo {
          padding: 10px;
          /* position: absolute; 
          top:0; 
          left:0; */
        }

        .hamburger {
          display: block;
          float: right;
          cursor: pointer;
          position: absolute;
          top: 1.1em;
          right: 0;
          margin-right: 1em;
        }

        .item-intro {
          display: flex;
          padding: 20px;
          align-items: center;
          font-family: Open Sans;
          font-style: normal;
          font-size: 18px;
          line-height: 24px;
          /* width: 200px; */
          width: auto;
        }
        
      }
